import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterLink } from '@angular/router';
import { HfButtonComponent } from '@monorepo/ui';
import { AppService, Cookie } from '@monorepo/utils';
import { environment } from '../../../environments/environment';
import { CookieHelper } from '../../shared/services/cookie.helper';
import { URLS } from '../../shared/utils/urls';

@Component({
  selector: 'app-not-found',
  standalone: true,
  templateUrl: './not-found.component.html',
  styleUrl: './not-found.component.scss',
  imports: [HfButtonComponent, RouterLink]
})
export class NotFoundComponent implements OnInit, OnDestroy {

  missingUrl: string = '';
  redirectTimeout: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public appService: AppService,
    public cookieHelper: CookieHelper
  ) { }

  ngOnInit() {
    this.missingUrl = this.route.snapshot.url.join('/');
    if (this.appService.isBrowserRendering) {
      let zipCode = this.cookieHelper.getCookie(Cookie.ZIP_CODE);
      this.redirectTimeout = setTimeout(() => {
        if (zipCode !== '') {
          this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/${zipCode}/0/0/4/100`]);
        } else {
          window.location.href = environment.client_url;
        }
      }, 3000);
    }
  }

  ngOnDestroy() {
    if (this.redirectTimeout) {
      clearTimeout(this.redirectTimeout);
    }
  }
}